html {
    overflow-x: hidden;
    // Fix font render
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
}

body {
    overflow-x: hidden;

    // Fix font & all icon special show correct example: "", !
    font-family: Arial, system-ui, sans-serif;

    // Button 
    // .w-button:hover {
    //     background: white;
    // }

    // .heading
    h1,h2,h3,h4,h5,h6,h7,h8,h9 {
        span {
            font-family: Testmartinaplantijn Light, Arial, sans-serif;
            font-style: italic;
        }
    }

    // Video 
    .w-background-video {
        position: unset;
        height: 100%;
        width: 100%;
    }

    .w-richtext {
        ul,ol {
            padding-left: 22px;
            text-align: left;
        }
        ul {
            list-style-type: none;
            text-align: left;
            li {
                position: relative;
                &:before {
                    content: "";
                    background: rgba(112, 112, 112, 1);
                    height: 4px;
                    width: 4px;
                    border-radius: 100%;
                    position: absolute;
                    left: -11px;
                    top: 50%;
                    transform: translate(-50%, -50%);
                }
            }
        }
    }

    // ul li global
    .content-in-richtext {
        ul,
        ol {
            padding: 0 0 0 25px;
            text-align: left;
        }
    }

    // Slider
    .swiper {
        div[class*="swiper-button-"] {
            background: #32c0cf;
            height: 72px;
            width: 72px;
            border-radius: 100%;
            top: 42.5%;
            border-radius: 100%;

            &.swiper-button-disabled {
                display: none;
            }

            &:after {
                content: "" !important;
                background: url("https://cdn.prod.website-files.com/695b0318a011b846bd6be9ac/69787ce4d479d8e7dc128faf_icon-right-black.svg") no-repeat center center;
                width: 24px;
                height: 24px;
                position: absolute;
                background-size: 18px 18px;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }

        .swiper-button-prev {
            &:after {
                transform: translate(-50%, -50%) rotate(180deg) !important;
            }
        }
    }

    .section-featured-projects {
        .button-swiper-next {
            cursor: pointer;
            position: absolute;
            margin-top: -23px;
            right: -40px;
            background: rgba(50, 192, 207, 1);
            width: 72px;
            height: 72px;
            border-radius: 50%;
            top: 50%;
            z-index: 2;
            transform: translateY(-50%);
            &:after {
                content: "" !important;
                background: url("https://cdn.prod.website-files.com/695b0318a011b846bd6be9ac/69787ce4d479d8e7dc128faf_icon-right-black.svg") center center / 18px 18px no-repeat;
                width: 24px;
                height: 24px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
        .swiper {
            // overflow: unset;

            .swiper-wrapper {
                // overflow: hidden;
                gap: unset;
            }

            .swiper-button-next {
                right: -35px;
                z-index: 9;
                opacity: 0;
            }

            .swiper-button-prev {
                left: 4px;
                top: 50%;
                z-index: 2;
                transform: translateY(-50%);
                margin-top: -23px;
            }
        }

        .project-heading-wrapper {
            a.button:hover {
                background: white;
            }
        }

        .project-item-featured {
            &:hover {
                .featured-heading a {
                    color: rgba(50, 192, 207, 1);
                }
            }
        }

        .featured-heading:hover {
            a {
                color: rgba(50, 192, 207, 1);
            }
        }

        .featured-tags {
            a {
                cursor: text;
            }
        }

        .featured-tags .tag-item {
            position: relative;
            padding-left: 10px;
            padding-right: 1px;

            &:first-child {
                padding-left: 0;

                &:before {
                    display: none;
                }
            }

            &:before {
                content: "";
                position: absolute;
                width: 2px;
                height: 2px;
                background: rgba(50, 192, 207, 1);
                border-radius: 100%;
                top: 50%;
                margin-left: -6px;
                transform: translate(-50%, -50%);
            }
        }

    }

    .w-slider-nav-invert>div.w-active {
        background-color: rgba(50, 192, 207, 1);
    }

    // Logo brand
    .section-brand-slider {
        // max-width: 100%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        width: 100%;
        overflow: hidden;

        // .swiper {
        //     width: 100%;
        // }

        // .swiper-free-mode>.swiper-wrapper {
        //     transition-timing-function: linear !important;
        //     will-change: transform;
        //     width: max-content;
        // }

        // .logo-list-wrapper {
        //     overflow-x: hidden;
        //     position: relative;
        //     width: 100%;
        // }
        
        // .wrap-logo.swiper {
        //     margin: 0;
        //     position: static;
        // }
        
        // .list-logo.swiper-wrapper {
        //     display: flex;
        //     align-items: center;
        // }
        
        // .logo-list-wrapper img {
        //     margin: 0 auto;
        // }
    }
    
    // Line effect
    .under-line-wrap {
        display: block;
        overflow: hidden;
        width: 100%;
        max-width: 269px;
        margin-top: 0.25rem;
    }

    .under-line-wrap.js-line-reveal:not(.reveal) {
        clip-path: inset(0 100% 0 0);
    }

    .under-line-wrap svg {
        display: block;
        height: auto;
    }

    .under-line-wrap.reveal {
        animation: line-reveal 3s cubic-bezier(0.22, 1, 0.36, 1) 0s forwards;
    }

    @keyframes line-reveal {
        0% {
            clip-path: inset(0 100% 0 0);
        }

        100% {
            clip-path: inset(0 0 0 0);
        }
    }


    /* ----- Icon-2: nếu có under-line-wrap js-line-reveal → dùng hiệu ứng draw (clip-path) như line ----- */
    .icon-2.under-line-wrap {
        max-width: 219px;
    }

    .icon-2 svg {
        display: block;
        width: 100%;
        height: auto;
    }

    .icon-2.js-reveal:not(.reveal):not(.under-line-wrap) {
        opacity: 0;
        transform: scale(0.92);
    }

    .icon-2.reveal:not(.under-line-wrap) {
        animation: icon-reveal 1.5s cubic-bezier(0.22, 1, 0.36, 1) forwards;
    }

    .icon-2.reveal:not(.under-line-wrap):nth-child(2) {
        animation-delay: 0.15s;
    }

    @keyframes icon-reveal {
        to {
            opacity: 1;
            transform: scale(1);
        }
    }

    // Footer
    .footer-link:hover {
        color: #40c1ce;
    }

    // Contact field
    form {
        .w-input:focus, .w-input:active, textarea:focus, textarea:active, .w-select:focus, .w-select:active {
            border-color: rgba(50, 192, 207, 1);
        }

        input[type="submit"]:focus {
            border: none;
        }

        input.is-error {
            border: 1px solid rgba(250, 12, 0, 1);
        }

        input.has-complete {
            background: rgba(230, 230, 230, 1);
        }
    }

}


@media screen and (max-width: 991px) {
    body {
        .section-featured-projects {
            .button-swiper-next {
                width: 52px;
                height: 52px;
                top: 37.5%;
                right: 5px;
                margin: 2px 0 0;
                &:after {
                    background-size: 8px;
                }
            }
            .swiper {
                overflow: hidden;

                .swiper-wrapper {
                    overflow: unset;
                }

                div[class*=swiper-button-] {
                    width: 52px;
                    height: 52px;
                    top: 37.5%;
                    margin: 4px 0 0;

                    &:after {
                        background-size: 8px;
                    }
                }

                .swiper-button-next {
                    right: 5px;
                }

                .swiper-button-prev {
                    left: 5px;
                }
            }
        }

        // section 
        section {
            .heading-fullwidth {
                br {
                    display: none;
                }
            }
        }

        // Footer
        .footer-wrapper {
            .footer-block {
                .footer-content-wrap {
                    display: none;
                }

                .icon-footer {
                    &:before {
                        content: "";
                        background: url("https://cdn.prod.website-files.com/695b0318a011b846bd6be9ac/69787ce351d5448cc4fdd025_icon-active-white.svg") no-repeat center center;
                        top: 3px;
                        right: 0;
                        background-size: 5px 8px;
                        transform: rotate(90deg);
                        position: absolute;
                        height: 10px;
                        width: 10px;
                    }
                }

                &.is-active {
                    border-top: 1px solid rgba(50, 192, 207, 1);

                    .icon-footer {
                        &:before {
                            background: url("https://cdn.prod.website-files.com/695b0318a011b846bd6be9ac/69787ce4abfd27f19ad60fe7_icon-blue-active.svg") no-repeat center center;
                            transform: rotate(-90deg);
                            background-size: 5px;
                            top: 5px;
                        }
                    }

                    .footer-title-wrap {
                        .title-small {
                            color: rgba(50, 192, 207, 1);
                        }
                    }

                    .footer-content-wrap {
                        display: block;
                        padding-top: 28px;
                        padding-bottom: 18px;
                    }
                }
            }
        }
    }
}