    object-fit: contain;
    animation: ecosystem-float 2.5s ease-in-out infinite;
  }
  .ecosystem-logos-mobile img:nth-child(5n+1) {
    animation-delay: 0s;
  }
  .ecosystem-logos-mobile img:nth-child(5n+2) {
    animation-delay: 0.2s;
  }
  .ecosystem-logos-mobile img:nth-child(5n+3) {
    animation-delay: 0.4s;
  }
  .ecosystem-logos-mobile img:nth-child(5n+4) {
    animation-delay: 0.6s;
  }
  .ecosystem-logos-mobile img:nth-child(5n+5) {
    animation-delay: 0.8s;
  }
  .ecosystem-logos-mobile img:nth-child(3n+1) {
    justify-self: start;
    margin-top: 0;
  }
  .ecosystem-logos-mobile img:nth-child(3n+2) {
    justify-self: center;
    margin-top: 12px;
  }
  .ecosystem-logos-mobile img:nth-child(3n) {
    justify-self: end;
    margin-top: -8px;
  }
  .ecosystem-logos-mobile img:nth-child(2) {
    margin-top: -30px;
  }
  .ecosystem-hub {
    min-width: unset;
  }
}
