
@media screen and (min-width: 992px) and (max-width: 1199px) {
    // Shopify landing
    .shopify-landing {
        .section-two-column.container {
            padding: 50px 0;

            .column-has-text {
                padding-right: 20px;

                .flex-block {
                    margin-bottom: 25px;
                }

                .heading-2 {
                    font-size: 50px;
                    margin-bottom: 30px;
                }

                .text-block-5 {
                    max-width: 80%;
                }
            }
        }

        .heading-faq {
            font-size: 60px;
        }

        .hero-without-image {
            padding: 60px 30px 80px;
        }

        .columns {
            &.has-text-right {
                .column-has-text.content-right {
                    padding-left: 80px;

                    .w-richtext {
                        max-width: 90%;
                    }
                }
            }
        }

        .faq-section {
            padding: 100px 0;
        }
    }

    // Contact page
    .section-contact-form {
        padding: 80px 0 100px 0;
        .left-contact {
            padding-right: 30px;
            .heading-h2-global {
                font-size: 50px;
            }
            .description-contact {
                max-width: 90%;
                font-size: 14px;
            }

            .list-contact-info {
                grid-column-gap: 0;
                grid-row-gap: 25px;
                
                .item-contact-phone {
                    overflow-wrap: anywhere;
                    word-break: break-word;
                }

                .item-contact {
                    .icon-contact {
                        margin-bottom: 20px;
                    }

                    &:nth-child(2n + 2){
                        padding-left: 25px;
                    }

                    .item-contact-heading {
                        font-size: 18px;
                    }
                    .item-contact-phone, .content-contact {
                        font-size: 14px;
                    }
                }
            }
        }

        .right-contact-form {
            padding: 60px 35px;
        }
    }
}

@media screen and (max-width: 767px){
    .section-contact-form {
        .item-contact:last-child {
            p > br {
                display: none;
            }
        }
    }
}