// Tablet
@media screen and (min-width: 992px) and (max-width: 1279px) {
    body {
        .nav-menu {
            .nav-has-mega {
                .mega-menu {
                    .menu_sub-wrap {
                        padding: 50px 0;
                        gap: 30px;

                        .left-panel {
                            width: 30%;

                            .menu-item {
                                padding: 20px 0;

                                a {
                                    font-size: 21px;
                                }
                            }
                        }

                        .right-panel {
                            width: 70%;

                            .menu-content-right {
                                gap: 25px;

                                .menu-item-link {
                                    gap: 20px;
                                    padding: 15px 20px;

                                    img {
                                        width: 25px;
                                        height: 25px;
                                    }

                                    .menu-content {
                                        h3 {
                                            font-size: 16px;
                                        }

                                        div {
                                            font-size: 14px;
                                        }
                                    }

                                    &.view-all {
                                        .menu-content {
                                            div {
                                                font-size: 14px;
                                            }
                                        }
                                    }

                                }
                            }
                        }

                    }
                }
            }
        }
    }
}


// Mobile menu
@media screen and (max-width: 991px) {
    html:has(.menu-button.w--open){
        overflow: hidden;
    }
    body {
        &:has(.w--open){
            .menu-button.w--open {
                img {
                    opacity: 0;
                }

                position: relative;
                &:after {
                    content: "";
                    position: absolute;
                    background: url("https://cdn.prod.website-files.com/695b0318a011b846bd6be9ac/697aca06fc1a5c8dfb9034ac_icon-close.svg") no-repeat;
                    background-color: white;
                    width: 24px;
                    height: 24px;
                    top: 0;
                    right: 0;
                }
            }

            .w-nav-overlay {
                height: 100vh !important;
                outline: 0;
                -webkit-backface-visibility: hidden;
                overflow: hidden;
                overflow-y: auto;
                overscroll-behavior: none;
                -webkit-overflow-scrolling: touch;

            }

            .nav-item-social {
                display: block;
                position: fixed;
                bottom: 0;
                left: 0;
                right: 0;
                background: white;
                z-index: 9;
                .social-wrapper{
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    padding: 28px 32px 45px;
                    border-top: 1px solid rgba(0, 0, 0, 0.1);
                    .left-social {
                        display: flex;
                        gap: 32px;
                        a {
                            position: relative;
                            font-size: 0;
                            width: 24px;
                            height: 24px;
                            display: inline-block;
                            &:after {
                                content: "";
                                position: absolute;
                                width: 24px;
                                height: 24px;
                                left: 0;
                            }
    
                            &:first-child:after {
                                background: url("https://cdn.prod.website-files.com/695b0318a011b846bd6be9ac/699e9663fab90d4bd5273b36_icon-instagram.svg") no-repeat;
                            }
    
                            &:last-child:after {
                                background: url("https://cdn.prod.website-files.com/695b0318a011b846bd6be9ac/699e9663b4e7a4dd631e9825_icon-linkedin.svg") no-repeat;
                            }
                        }
                    }
    
                    .right-social {
                        a {
                            color: #32c0cf;
                            position: relative;
                            &::after {
                                content: "";
                                position: absolute;
                                width: 100%;
                                height: 1px;
                                left: 0;
                                bottom: 2px;
                                background: #32c0cf;
                            }
                        }
                    }
                }
            }
        }
        .navbar {
            .header-menu {
                .main-mega-menu {
                    display: none;
                }

                .menu-button {
                    display: flex;

                    &.w--open {
                        .w-icon-nav-menu {
                            width: 24px;
                            height: 24px;

                            &:before {
                                content: "";
                                background: url("https://cdn.prod.website-files.com/695b0318a011b846bd6be9ac/697aca06fc1a5c8dfb9034ac_icon-close.svg") no-repeat;
                                background-size: 24px;
                                height: 24px;
                                width: 24px;
                                position: absolute;
                                left: 0;
                            }
                        }

                    }
                }
            }

            .w-nav-overlay {
                [data-nav-menu-open] {
                    width: 100%;
                    height: 100%;
                    background: white;
                    padding-top: 19px;
                    overflow-y: scroll;
                    max-height: calc(100vh - 100px);
                    padding: 19px 0 250px;

                    .nav-item {
                        width: 100%;

                        &:last-child {
                            .menu-item-parents {
                                border: none;
                            }
                        }

                        &.nav-has-mega {
                            .menu-has-child {
                                .w--nav-link-open {
                                    font-family: "Testmartinaplantijn Light", Arial, sans-serif;
                                    font-style: italic;
                                }
                            }
                        }

                        .menu-item-parents {
                            border-bottom: 1px solid rgba(0, 0, 0, 0.1);
                            margin: 0 32px;
                        }

                        .mega-menu {
                            display: none !important;
                        }

                        .w-nav-link {
                            font-family: Test Martina Plantijn;
                            font-weight: 300;
                            font-style: Light;
                            font-size: 44px;
                            line-height: 99%;
                            letter-spacing: -1px;
                            vertical-align: middle;
                            color: rgba(0, 0, 0, 1);
                            padding: 28px 0;
                            margin: 0;
                            max-width: 90%;
                        }

                    }

                    .nav-has-mega {
                        .menu-has-child {
                            display: flex;
                            align-items: center;
                            justify-content: space-between;

                            &:hover {
                                .mega-menu {
                                    display: none;
                                }
                            }

                            a {
                                width: 100%;
                                max-width: 90%;

                                &:after {
                                    display: none;
                                }
                            }

                            .button-icon {
                                width: 24px;
                                height: 24px;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                margin-right: -7px;
                            }
                        }

                        &.is-active {
                            background: rgba(244, 244, 244, 1);
                            padding: 0 0 30px 0;
                            text-align: left;

                            // &:first-child {
                            //     // padding-top: 12px;
                            // }

                            .w-nav-link.active-link {
                                color: #32c0cf;
                                font-family: "Testmartinaplantijn Light", Arial, sans-serif;
                                font-style: italic;
                            }


                            .menu-item-parents {
                                border-bottom: none;

                                &:has(.active-link) {
                                    .button-icon {
                                        &:before {
                                            left: 50%;
                                            top: 50%;
                                            // transform: translate((-50%, -50%), roate(-90deg));
                                            transform: translate(-50%, -50%) rotate(180deg);
                                        }
                                    }
                                }

                                img {
                                    display: none;
                                }

                                .button-icon {
                                    width: 24px;
                                    height: 24px;
                                    background: rgba(244, 244, 244, 1);
                                    position: relative;
                                    margin-right: -5px;

                                    &:before {
                                        content: "";
                                        background: url("https://cdn.prod.website-files.com/695b0318a011b846bd6be9ac/697ac84e110466abb5e6c73e_iconDownArrowBold-teal.svg") no-repeat;
                                        background-size: 12px 12px;
                                        position: absolute;
                                        height: 12px;
                                        width: 12px;
                                        left: 50%;
                                        top: 50%;
                                        transform: translate(-50%, -50%);
                                    }
                                }
                            }

                            .menu-child-wrapper {
                                display: block;
                                margin-top: 15px;

                                .menu-level-2 {
                                    padding: 27px 32px;
                                    background: #fff;
                                    margin: 0 32px 13px;

                                    &.is-active {
                                        background: #32c0cf;
                                        padding: 27px 32px 39px 32px;

                                        .button-icon {
                                            width: 24px;
                                            position: relative;
                                            height: 24px;
                                            margin-right: -5px;
                                            margin-top: 4px;

                                            img {
                                                display: none;
                                            }

                                            &:after {
                                                content: "";
                                                background: url("https://cdn.prod.website-files.com/695b0318a011b846bd6be9ac/697ae341ee48fe882c42da87_iconDownArrowBoldWhite.svg") no-repeat;
                                                position: absolute;
                                                width: 13px;
                                                height: 13px;
                                                background-repeat: no-repeat;
                                                left: 50%;
                                                top: 50%;
                                                transform: translate(-50%, -50%);
                                                background-position: top center;
                                            }
                                        }
                                    }

                                    .menu-level3 {
                                        display: flex;
                                        justify-content: space-between;
                                        width: 100%;

                                    }

                                    &.has-group {
                                        display: flex;
                                        align-items: center;
                                        justify-content: space-between;
                                        text-align: left;

                                        &.is-active {
                                            display: block;
                                            background: rgba(50, 192, 207, 1);

                                            .menu-level3 {
                                                margin-bottom: 15px;

                                                a {
                                                    color: white;
                                                    font-family: Lexend;
                                                    font-weight: 400;
                                                    font-size: 18px;
                                                    line-height: 160%;
                                                    letter-spacing: 0px;
                                                    max-width: 90%;
                                                    display: block;
                                                    width: 100%;

                                                }
                                            }

                                            .menu-wrapper-level3 {
                                                display: block;

                                                a {
                                                    font-family: Lexend;
                                                    font-weight: 400;
                                                    font-size: 16px;
                                                    line-height: 160%;
                                                    letter-spacing: 0px;
                                                    color: white;
                                                    display: block;
                                                    padding: 15px 0;
                                                    border-top: 1px solid rgba(0, 0, 0, 0.1);
                                                }

                                                .w-button {
                                                    width: 100%;
                                                    padding: 12px 0;
                                                    margin-top: 8px;
                                                }
                                            }
                                        }
                                    }

                                    a {
                                        font-family: Lexend;
                                        font-weight: 400;
                                        font-size: 18px;
                                        line-height: 160%;
                                        letter-spacing: 0px;
                                        width: 100%;
                                        color: rgba(0, 0, 0, 1);
                                        text-decoration: none;
                                    }

                                    .menu-wrapper-level3 {
                                        display: none;
                                    }
                                }
                            }
                        }

                        .menu-child-wrapper {
                            display: none;
                        }
                    }
                }
            }
        }
    }
}