}

// Section list question
.columns-faqs {
    div.w-col {
        counter-reset: step;

        &.has-number {
            .heading-item-faqs::before {
                content: counter(step);
                margin-right: 20px;
                background: black;
                width: 24px;
                height: 24px;
                display: inline-block;
                border-radius: 100%;
                color: white;
                justify-content: center;
                text-align: center;
                align-items: center;
                font-weight: 500;
                line-height: 138%;
            }
        }

        .dropdown {
            position: relative;
            counter-increment: step;

            &:has(.w--open) {
                border-top: 1px solid rgba(50, 192, 207, 1);

                .w-icon-dropdown-toggle:after {
                    background: url("https://cdn.prod.website-files.com/695b0318a011b846bd6be9ac/697721f79d74677fe53324ca_icon-active.svg");
                    margin-top: 4px;
                }

                .w-richtext {
                    margin-top: 9px;

                    p {
                        margin-bottom: 11px;
                    }

                    .button-2 {
                        color: #fff;
                        text-align: center;
                        vertical-align: middle;
                        background-color: #000;
                        border-radius: 4px;
                        flex-flow: column;
                        justify-content: center;
                        width: auto;
                        height: auto;
                        margin-top: 0;
                        margin-left: auto;
                        margin-right: auto;
                        padding: 12px 16px;
                        font-family: "Lexend", sans-serif;
                        font-size: 16px;
                        font-weight: 400;
                        line-height: 160%;
                        text-decoration: none;
                        display: flex;
                    }
                }

            }

            .w-icon-dropdown-toggle {
                &:before {
                    display: none;
                }

                &:after {
                    content: "";
                    background: url("https://cdn.prod.website-files.com/695b0318a011b846bd6be9ac/697721f7340c546d663da6f0_icon-down.svg");
                    background-size: 8px 8px;
                    width: 8px;
                    height: 8px;
                    position: absolute;
                    right: 0;
                }
            }

            .dropdown-toggle.w--open {
                .heading-item-faqs {
                    color: rgba(50, 192, 207, 1);

                    &::before {
                        background: rgba(50, 192, 207, 1);
                    }
                }
            }

           
        
        }
    }
}


