// Section featurs Item
.section-all-features {
    .main-features-wrapper {
        .item-featues:hover {
            background-color: #32c0cf;
            color: #fff;
            padding: 40px 42px;
            border-radius: 4px;
            cursor: pointer;
            * {
                color: #fff;
            }

            .icon-features-item {
                img:first-child {
                    display: none;
                }
                img:last-child {
                    display: block;
                }
            }
        }
    }
}

// Section migration slider
.section-migration-slider {
    .swiper-wrapper {
        display: flex;
    }

    .swiper div[class*=swiper-button-] {
        top: 50%;
        transform: translateY(-50%);
        margin: -6px 0 0 0;
    }

    .swiper-button-next {
        opacity: 0;
    }

    .right-slider-migrate {
        .button-swiper-next {
            cursor: pointer;
            position: absolute;
            margin-top: -6px;
            right: -33px;
            background: black;
            width: 72px;
            height: 72px;
            border-radius: 50%;
            top: 50%;
            z-index: 2;
            transform: translateY(-50%);
            &:after {
                content: "" !important;
                background: url("https://cdn.prod.website-files.com/695b0318a011b846bd6be9ac/69787ce4abfd27f19ad60fe7_icon-blue-active.svg") center center / 18px 18px no-repeat;
                width: 24px;
                height: 24px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }
}

// Section project
.section-project-brief {
    .brief-content {
        .brief-left {
            .w-richtext {
                p:last-child {
                    margin-bottom: 0;
                }
            }
        }

        .brief-right {
            .brief-content-infor {
                a[href] {
                    text-decoration: none;
                    position: relative;
                    &:after {
                        content: "";
                        position: absolute;
                        bottom: -8px;
                        left: 0;
                        width: 100%;
                        height: 1px;
                        background: #32c0cf;
                    }

                    &:hover {
                        &:after {
                            display: none;
                        }
                    }
                }
            }
        }
    }
}
