                    font-size: 24px;
                }

                .richtext-features {
                    font-size: 14px;
                }
            }
        }

        // Section migrated data
        .section-migrated-data {
            padding: 100px 0;
            .heading-h2-global {
                font-size: 50px;
            }
        }

        // Section migration slider
        .section-migration-slider {
            .left-migrate-slider {
                padding-right: 50px;
                .heading-h2-migrated {
                    font-size: 50px;
                    max-width: 100%;
                    line-height: normal;
                }
            }

            .swiper-slide.migration-item {
                padding: 50px;
            }
        }

        // Section without image
        .hero-without-image.js-line-section.custom-spacing {
            padding: 120px 0;;
        }

        // Footer
        .footer-content {
            grid-column-gap: 10px;
            grid-row-gap: 10px;
        }
    }
}

@media screen and (min-width: 992px) and (max-width: 1199px) {
    .section-trusted-featured {
        .features-list.features-wrap-two {
            grid-column-gap: 0;
            grid-row-gap: 0;
        }
    }

    .faq-section {
        .under-line-wrap.js-line-reveal {
            margin-top: 0;
            display: block;
            left: 0;
            position: absolute;
            bottom: -7px;
        }
    }
    
}

@media screen and (min-width: 768px) and (max-width: 991px) {
    body {
        .section-trusted-featured {
            .features-right.item-values-cards.swiper {
                padding: 0;
            }

            .swiper-wrapper.item-truessted-wrapper {
                gap: 0;
            }

            .swiper div[class*=swiper-button-] {
                background: #32c0cf;
                height: 40px;
                width: 40px;
                top: 50%;
            }
        }
    }
}

@media screen and (max-width: 767px) {
    body {
        .section-trusted-featured {
            .item-values-cards {
                display: block;
                overflow: hidden;
                margin: 0 -16px;
                padding: 0 16px;
            }
    
            .item-values-cards .swiper-wrapper {
                display: flex;
                gap: 0;
                align-items: stretch;
            }
    
            .values-card {
                height: 100%;
            }
    
            .swiper-slide {
                height: auto;
            }
        }

        .section-list-services {
            .item-service {
                .service-panel-rollover {
                    max-width: 100%;
                }
            }
        }
    }

}